import React, { useState, useMemo, useRef } from 'react'

import { useLocation } from 'react-router-dom'

import { useMedia } from 'hooks/useMedia'
import { useOnClickOutside } from 'hooks/useOnClickOutside'

import { NavLink } from '../Link'
import { navMenuList } from './data'
import * as Styled from './Navbar.style'

export const Navbar = () => {
  const { pathname } = useLocation()

  const isMediaLg = useMedia('(min-width: 1238px)')

  const [isOpen, setOpen] = useState(false)
  const navRef = useRef(null)

  useOnClickOutside(navRef, () => {
    if (navRef && isOpen === true) {
      setOpen(false)
    }
  })

  const handleClose = React.useCallback(() => {
    setOpen(false)
  }, [])

  const renderMenu = useMemo(() => {
    return navMenuList
      .filter(({ isWideOnly }) => !isWideOnly || isMediaLg)
      .map(({ link, label }) => (
        <NavLink to={link} key={link} active={pathname === link}>
          {label}
        </NavLink>
      ))
  }, [pathname, isMediaLg])

  return (
    <nav ref={navRef}>
      <Styled.DesktopNavbar>{renderMenu}</Styled.DesktopNavbar>
      {isOpen && (
        <Styled.MobileNavbar onClick={handleClose}>
          {renderMenu}
        </Styled.MobileNavbar>
      )}
    </nav>
  )
}
